.reset();

/*Font family*/

@font-face {
    font-family: 'ProximaNova_Regular';
    src: url('../fonts/ProximaNova-Regular.eot');
    src: url('../fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/ProximaNova-Regular.woff2') format('woff2'),
        url('../fonts/ProximaNova-Regular.woff') format('woff'),
        url('../fonts/ProximaNova-Regular.ttf') format('truetype'),
        url('../fonts/ProximaNova-Regular.svg#ProximaNova-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'ProximaNova_Bold';
    src: url('../fonts/ProximaNova-Bold.eot');
    src: url('../fonts/ProximaNova-Bold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/ProximaNova-Bold.woff2') format('woff2'),
        url('../fonts/ProximaNova-Bold.woff') format('woff'),
        url('../fonts/ProximaNova-Bold.ttf') format('truetype'),
        url('../fonts/ProximaNova-Bold.svg#ProximaNova-Bold') format('svg');
    font-weight: bold;
    font-style: normal;
}

.reg(){
    font-family: 'ProximaNova_Regular', sans-serif;
}

.bold(){
    font-family: 'ProximaNova_Bold', sans-serif;
}

/* Colors */
@btn-orange: #feb404;
@green-light: #49bf15;
@green-dark: #286c0a;

/* Settings*/
body{
    .reg();
}

html{
    font-size: 10px;
}

button, input, a:focus{
    outline: none;
}

.btn{
    border-radius: 8px;
    border: none;
}

.wrapper{
    .wrapper()
}

.title{
    font-size: 3.6rem;
    .bold();
    text-align: center;
}

.hvr-fade:hover, .hvr-fade:focus, .hvr-fade:active{
    background: #ffa600;
}


/*Оформление форм*/

.form{
    &-input {
        display: block;
        margin: 0 auto;
        width: 100%;
        height: 55px;
        margin-bottom: 2rem;
        border: 1px solid #dddddd;
        border-radius: 5px;
        font-size: 1.8rem;
        text-align: center;
    }

    label {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        padding: 0.5rem;
        background-color: #fff;
        font-size: 1.3rem;
        color: #565656;
    }
}

/*Оформление модальных окон*/
.popup {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.5);

    &-content {
        position: fixed;
        text-align: center;
        left: 50%;
        top: 10%;
        transform: translateX(-50%);
        padding: 2rem 4rem;
        background: #fff;
        .size(4);
        .size-md(5);
        .size-xs(8);
    }

    &-close {
        position: absolute;
        top: -1rem;
        right: -2rem;
        width: 2rem;
        text-align: center;
        border: none;
        background-color: transparent;
        font-size: 3rem;
        color: #fff;
        cursor: pointer;
    }
}

.popup-header {
    padding-top: 3.5rem;
    font-size: 1.8rem;
    .bold();
}

.popup-description {
    font-size: 1.6rem;
}

.popup-form {
    text-align: center;
    padding: 4rem;
    background: #fff;
    margin: 0 auto;

    &-input {
        display: block;
        margin: 0 auto;
        width: 100%;
        height: 55px;
        margin-bottom: 2rem;
        border: 1px solid #dddddd;
        border-radius: 5px;
        font-size: 1.8rem;
        text-align: center;
    }

    label {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        padding: 0.5rem;
        background-color: #fff;
        font-size: 1.2rem;
        color: #565656;
    }

}

/*Спасибо за обращение*/

.popup-thanks{
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.5);

    &-content {
        position: fixed;
        text-align: center;
        left: 50%;
        top: 35%;
        transform: translateX(-50%);
        padding: 5rem;
        background: #fff;
        .size(3);
        .size-md(3);
        .size-sm(5);
        .size-xs(6);
    }

    &-close {
        position: absolute;
        top: -1rem;
        right: -2rem;
        width: 2rem;
        text-align: center;
        border: none;
        background-color: transparent;
        font-size: 3rem;
        color: #fff;
        cursor: pointer;
    }

    &-title{
        font-size: 1.8rem;
        .bold();
        padding-bottom: 2rem;    
    }

    &-subtitle{
        font-size: 1.8rem;
        .bold();
    }
}



