/*Оформление форм*/

.form-input {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 55px;
  margin-bottom: 2rem;
  border: 1px solid #dddddd;
  border-radius: 5px;
  font-size: 1.8rem;
  text-align: center;
}

.form label {
  position: absolute;
  transform: translateX(-50%) translateY(-50%);
  padding: 0.5rem;
  background-color: #fff;
  font-size: 1.3rem;
  color: #565656;
}

/*Оформление модальных окон*/

.popup {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-content {
  position: fixed;
  text-align: center;
  left: 50%;
  top: 10%;
  transform: translateX(-50%);
  padding: 2rem 4rem;
  background: #fff;
  width: calc(33.333333333333336% - 10px);
}

.popup-close {
  position: absolute;
  top: -1rem;
  right: -2rem;
  width: 2rem;
  text-align: center;
  border: none;
  background-color: transparent;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}

.popup-header {
  padding-top: 3.5rem;
  font-size: 1.8rem;
  font-family: 'ProximaNova_Bold', sans-serif;
}

.popup-description {
  font-size: 1.6rem;
}

.popup-form {
  text-align: center;
  padding: 4rem;
  background: #fff;
  margin: 0 auto;
}

.popup-form-input {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 55px;
  margin-bottom: 2rem;
  border: 1px solid #dddddd;
  border-radius: 5px;
  font-size: 1.8rem;
  text-align: center;
}

.popup-form label {
  position: absolute;
  transform: translateX(-50%) translateY(-50%);
  padding: 0.5rem;
  background-color: #fff;
  font-size: 1.2rem;
  color: #565656;
}

