}

/*Как мы работаем*/

.how-we-work {
  background: url('../img/how-we-work/bg-work.jpg') no-repeat center;
  background-size: cover;
}

.how-we-work-container {
  display: flex;
  justify-content: space-between;
  padding-top: 12rem;
  padding-bottom: 13rem;
  flex-wrap: wrap;
}

.how-we-work-titles {
  width: calc(41.66666666666667% - 10px);
  padding-top: 4rem;
}

.how-we-work-title {
  font-size: 4rem;
  font-family: 'ProximaNova_Bold', sans-serif;
  text-transform: uppercase;
}

.how-we-work-question,
.how-we-work-descr {
  font-size: 2rem;
}

.how-we-work-question {
  padding-bottom: 1.5rem;
}

.how-we-work-form {
  width: calc(29.166666666666668% - 10px);
  text-align: center;
  padding: 4rem 5rem;
  background: #fff;
}

.how-we-work-form form {
  margin-top: 4rem;
}

.how-we-work-form-header {
  font-size: 1.8rem;
}

.how-we-work-form-btn {
  margin-bottom: 1.5rem;
  display: block;
  width: 100%;
  height: 6.5rem;
  background-color: #feb404;
  border: 1px solid #ffba15;
  box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
  text-transform: uppercase;
  font-size: 1.6rem;
  color: #fff;
  font-family: 'ProximaNova_Bold', sans-serif;
  cursor: pointer;
}

.how-we-work-form small {
  font-size: 1.3rem;
  color: #565656;
}

/*Дополниельные услуги*/

.extra-services {
  background: #286c0a;
}

.extra-services-titles {
  text-align: center;
  color: #fff;
  padding-top: 12rem;
  padding-bottom: 7rem;
}

.extra-services-title {
  font-size: 3.6rem;
  font-family: 'ProximaNova_Bold', sans-serif;
  padding-bottom: 5rem;
}

.extra-services-subtitle {
  font-size: 1.9rem;
}

.extra-services-items {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 11rem;
}

.extra-services-item {
  text-align: center;
  width: calc(33.333333333333336% - 10px);
}

.extra-services-item-name {
  padding: 3rem 0;
  font-size: 2rem;
  color: #fff;
}

.extra-services-item-btn {
  padding: 1.5rem 4rem;
  font-size: 1.6rem;
  font-family: 'ProximaNova_Bold', sans-serif;
  text-transform: uppercase;
  background: #feb404;
  color: #fff;
  cursor: pointer;
}

/*Контакты*/

.contacts {
  background: url('../img/contacts/contacts-bg.jpg') no-repeat center;
  background-size: cover;
}

.contacts-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contacts-info {
  padding-top: 8rem;
  padding-bottom: 8rem;
  width: calc(50% - 10px);
}

.contacts-info-title {
  font-size: 3.6rem;
  font-family: 'ProximaNova_Bold', sans-serif;
  padding-bottom: 7rem;
}

.contacts-info-map {
  width: 100%;
  padding-bottom: 2rem;
}

.contacts-info-adress,
.contacts-info-phone,
.contacts-info-email,
.contacts-info-requisites {
  font-size: 1.8rem;
  padding-bottom: 4rem;
  line-height: 3.5rem;
}

.contacts-info-adress strong,
.contacts-info-phone strong,
.contacts-info-email strong,
.contacts-info-requisites strong {
  font-family: 'ProximaNova_Bold', sans-serif;
}

.contacts-info-email span {
  text-decoration: underline;
}

.contacts-form {
  text-align: center;
  padding: 4rem 5rem;
  background: #fff;
  width: calc(29.166666666666668% - 10px);
  margin-top: 8rem;
  margin-bottom: 28rem;
}

.contacts-form-header {
  font-size: 1.8rem;
}

.contacts-form form {
  margin-top: 4rem;
}

.contacts-form-btn {
  margin-bottom: 1.5rem;
  display: block;
  width: 100%;
  height: 6.5rem;
  background-color: #feb404;
  border: 1px solid #ffba15;
  box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
  text-transform: uppercase;
  font-size: 1.6rem;
  color: #fff;
  font-family: 'ProximaNova_Bold', sans-serif;
  cursor: pointer;
}

.contacts-message {
  height: 9rem;
}

/*Футер*/

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.footer-logo {
  padding: 4rem 0;
}

.footer-contacts {
  width: calc(25% - 10px);
  text-align: right;
}

.footer-contacts-phone {
  font-size: 2.4rem;
  font-family: 'ProximaNova_Bold', sans-serif;
}

.footer-contacts-email,
.footer-contacts-schedule {
  font-size: 1.7rem;
  text-decoration: underline;
}

/*Адаптив*/

