@import 'smart-grid.less';
@import 'settings.less';

.main {
    background: #fff url(../img/main-bg.jpg) center center no-repeat;
    background-size: cover;
}

/* Header */

.header {
    padding-top: 10px;
    .row-flex();
    .justify-content(space-between);
    .justify-content-sm(center);
}

.phone-section {
    .size(5);
    .size-md(8);
    .size-sm(12);
    .row-flex();
    .justify-content(flex-end);
    .justify-content-md(space-around);
    .justify-content-sm(space-around);
    align-items: center;
    flex-wrap: wrap;

    /* Phone */

    span {
        font-size: 2rem;
        .bold();
        margin-right: 30px;
    }

    /* Button */

    button {
        .size(6);
        .size-md(4);
        .size-sm(4);
        .size-xs(6);
        height: 50px;
        box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
        border: 1px solid #ffba15;
        background: @btn-orange;
        text-transform: uppercase;
        color: #fff;
        font-size: 1.2rem;
        cursor: pointer;
    }
}


.main-content {
    padding-top: 10rem;
    .row-flex();
    justify-content: space-between;
    align-items: flex-start;
}

.main-information {
    .size(5);
    .size-md(12);

    .main-title {
        font-size: 4rem;
        .bold();
        padding-bottom: 5rem;
        line-height: 5.8rem;
    }

    .main-subtitle {
        font-size: 2.4rem;
        padding-bottom: 6rem;
    }

    .main-price {
        display: inline-block;
        padding: 3rem 4rem;
        text-align: center;
        font-size: 1.5rem;
        color: #fff;
        background: @green-light;
        margin-bottom: 6rem;

        strong {
            .bold();
            font-size: 2rem;
        }

    }

    .main-trigger {
        font-size: 2.2rem;
        text-transform: uppercase;
        .bold();
    }
}

.main-form {
    text-align: center;
    padding: 4rem 5rem;
    background: #fff;
    .size(3.3);
    .size-md(4.5);
    .size-sm(6);
    .size-xs(7);

    form {
        margin-top: 4rem;
    }

    &-header {
        font-size: 1.8rem;

        strong {
            .bold();
        }
    }



    &-btn {
        margin-bottom: 1.5rem;
        display: block;
        width: 100%;
        height: 6.5rem;
        background-color: @btn-orange;
        border: 1px solid #ffba15;
        box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
        text-transform: uppercase;
        font-size: 1.6rem;
        color: #fff;
        .bold();
        cursor: pointer;
    }

    small {
        font-size: 1.2rem;
        color: #565656;
    }


}

.main-form-footnote {
    text-align: right;
    font-size: 1.2rem;
    margin-right: 9%;
    margin-top: -2rem;
    padding-bottom: 1.5rem;
}

/*Преимущества*/
.features {
    margin-bottom: 14rem;
}

.section-features-header {
    margin-top: 10rem;
    margin-bottom: 13rem;
    text-align: center;
    font-size: 3.6rem;
    .bold();
}

.features-slider {
    margin-bottom: 1.4rem;
}

.features {
    &-block {
        text-align: center;
    }

    &-img {

        img {
            display: block;
            margin: 0 auto;
        }
    }

    h3 {
        margin-top: 6.5rem;
        margin-bottom: 3.5rem;
        .bold();
        font-size: 2rem;
    }

    p {
        font-size: 1.6rem;
        line-height: 2rem;
        display: inline-block;
        width: 80%;
    }
}

.features-block {
    p a {
        text-decoration: none;
        color: @green-light;
    }
}

/*Слайдер преимуществ*/

.slick-active,
.slick-slide {
    outline: none;
}


.prev {
    position: absolute;
    top: 50%;
    left: 2%;
    transform: translateY(-50%);
    z-index: 99;
    width: 2rem;
    height: 2rem;
    border: none;
    background: url('../img/left-arrow.png') center no-repeat;
    background-size: 70%;
    cursor: pointer;
}

.next {
    position: absolute;
    top: 50%;
    right: 2%;
    transform: translateY(-50%);
    z-index: 99;
    width: 2rem;
    height: 2rem;
    border: none;
    background: url('../img/right-arrow.png') center no-repeat;
    background-size: 70%;
    cursor: pointer;
}



/*Цены*/
.price {
    background: @green-dark url('../img/price/green_bg.jpg') center;
    padding-bottom: 14rem;
}

.price-header {
    padding-top: 12rem;
    padding-bottom: 9rem;
    .row-flex();
    align-items: center;
    .justify-content(flex-end);
    .justify-content-md(center);


    .price-titles {
        .size(4);
        .size-md(12);
        text-align: center;
        color: #fff;
    }
}

.price-header-title {
    font-size: 3.6rem;
    .bold();
    padding-bottom: 5rem;
}

.price-header-subtitle {
    font-size: 1.8rem;

}

.price-btn-wrap {
    .size(4);
    .size-md(12);
    text-align: right;
}

.price-btn {
    background: @btn-orange;
    color: #fff;
    padding: 2rem 3rem;
    font-size: 1.8rem;
    cursor: pointer;
}

.price-items {
    .row-flex();
    .justify-content(center);
}

.price-item {
    .size(3);
    .size-md(6);
    .size-sm(8);
    .size-xs(10);
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    margin-right: 1rem;
    margin-left: 1rem;
    padding: 3rem;
    box-shadow: 0 14px 21px rgba(0, 0, 0, 0.17);

    &-img {
        width: 100%;
        max-width: 30rem;
    }

}

.price-item-content {
    padding-bottom: 3.5rem;

    .price-item-title {
        font-size: 1.9rem;
        .bold();
        padding-bottom: 3.5rem;
    }

    .price-item-cost {
        font-size: 1.9rem;
        .bold();
        padding: 3.5rem 0;

        strong {
            font-size: 3rem;
        }
    }

    .price-item-worklist {
        font-size: 1.5rem;
        line-height: 3rem;


        i {
            color: @green-dark;
            font-size: 2rem;
            padding-right: 0.2rem;
        }
    }
}

.price-item-btn {
    background: @btn-orange;
    color: #fff;
    box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
    padding: 2rem 5rem;
    font-size: 1.8rem;
    cursor: pointer;
}

/*Отзывы*/
.feedback {
    position: relative;
    background: url('../img/feedback/feedback-bg.jpg') no-repeat center;
    background-size: cover;

    &-prev-btn {
        left: -4rem;
    }

    &-next-btn {
        right: -4rem;
    }

    &-slider {
        box-shadow: 0 12px 18px rgba(0, 0, 0, 0.11);
        background-color: #ffffff;
        padding: 5rem 6rem;
        .size(11);
        .size-xs(12);
        margin: 0 auto;
        margin-bottom: 11rem;

        &-btn {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            border: none;
            background: transparent;
            cursor: pointer;

            img {
                width: 70%;
            }
        }
    }
}

.feedback-header {
    padding-top: 11.5rem;
    padding-bottom: 11rem;
    font-size: 3.6rem;
    text-align: center;
    .bold();
}

.wrap-slider {
    .size(12);
    display: flex;
    flex-wrap: wrap;
    .justify-content(space-between);
    .justify-content-md(center);
}

.fotorama {
    .size(6);
    .size-md(12);
}

.fotorama-description {
    .size(6);
    .size-md(12);

    &_item {
        font-size: 1.6rem;
        line-height: 3rem;

        strong {
            .bold();
        }
    }

}

.client {
    display: flex;
    margin-top: 3rem;
}

.client-avatar {
    margin-right: 2rem;
}

.client-place {

    .client-position {
        font-size: 1.6rem;
    }

    .client-name {
        font-size: 1.6rem;
        .bold()
    }

}

.feedback-text {
    position: relative;
    margin-top: 2rem;
    padding: 2rem;
    font-style: italic;
    font-size: 1.5rem;
    border: 2px solid @green-light;

    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -2rem;
        left: 4rem;
        width: 2rem;
        height: 1.8rem;
        background-color: #fff;
        border: 2px solid @green-light;
        border-bottom: none;
        border-top-right-radius: 100px;
    }

}

.feedback-form-wrap{
    padding-bottom: 10rem;
}

.feedback-form {
    .size(8);
    .size-md(7);
    .size-sm(6);
    .size-xs(8);
    margin: 0 auto;
    padding: 5rem 14rem;
    background: #fff;
    text-align: center;
}

.feedback-form-header {
    font-size: 2.2rem;
    text-align: center;
    .bold();
    padding-bottom: 4rem;
}

.feedback-form-btn{
    .size(4);
    .size-md(8);
    .size-sm(8);
    .size-xs(10);
    display: block;
    height: 6.5rem;
    background-color: @btn-orange;
    border: 1px solid #ffba15;
    box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
    text-transform: uppercase;
    font-size: 1.6rem;
    color: #fff;
    .bold();
    cursor: pointer;
    margin: 0 auto;
}

.wrap-input{
    display: flex;
    justify-content: center;
}

.wrap-input_item{
    .size(12);
}

.feedback-form-input{
    .size(10);
    .size-md(12);
    .size-sm(12);
}

.hand{
    position: absolute;
    bottom: 0;
    left: 0;
    max-width: 30rem;
}

/*Схема работы*/
.work-scheme-titles{
    text-align: center;
}

.work-scheme-header{
    font-size: 3.1rem;
    .bold();
    padding-top: 12rem;
}

.work-scheme-subtitle{
    font-size: 1.6rem;
    padding-top: 6.5rem;
}

.work-scheme-items{
    display: flex;
    .justify-content(flex-start);
    .justify-content-md(center);
    flex-wrap: wrap;
    padding-bottom: 12rem;
}

.work-scheme-item{
    .size(3);
    .size-md(4);
    .size-sm(8);
    .offset-left(1);
    .offset-left-md(2);
    .offset-left-sm(0);
    text-align: left;
    padding-top: 6.5rem;

    &-title{
        font-size: 1.8rem;
        .bold();
        padding-top: 4rem;
        padding-bottom: 2.5rem;
    }

    &-decr{
        font-size: 1.7rem;
    }
}

.bg-work-scheme{
    background: url('../img/work/spatel.png') no-repeat;
    background-position: 100% 100%;
}


/*Все на 100%*/
.all-in-100{
    background-color: #f5f5f5;
}

.all-in-100-titles{
    padding-top: 12rem;
    padding-bottom: 10rem;
    text-align: center;

}

.all-in-100-title{
    color: @green-light;
    font-size: 3.3rem;
    .bold();
    padding-bottom: 5rem;
}

.all-in-100-subtitle{
    font-size: 1.6rem;

}

.all-in-100-items{
    display: flex;
    .justify-content(flex-end);
    .justify-content-md(center);
    flex-wrap: wrap;
}

.all-in-100-item{
    .size(3);
    .size-md(4);
    .size-sm(12);
    margin-bottom: 10rem;
    margin-left: 1rem;
    display: flex;
    .justify-content(flex-start);
    align-items: center;
}

.all-in-100-item-img{
    padding-right: 2rem;
}

.all-in-100-item-descr{
    font-size: 1.6rem;
    
    strong{
        .bold();
    }
}

/*Мы развеем все опасности*/

.dangerous-title{
    padding-top: 12rem;
    padding-bottom: 7rem;
    text-align: center;
    font-size: 3.6rem;
    .bold();
}

.dangerous-variants{
    display: flex;
    flex-wrap: wrap;
    .justify-content(space-between);
    .justify-content-md(center);
    padding-bottom: 13rem;
}


/*Блок с проблемами*/
.dangerous-problems{
    .size(4.5);
    .size-md(10);
    border: 4px solid #e8e8e8;
    padding: 5rem 0;
    padding-left: 5rem;
    margin-bottom: 1.5rem;
    

    &_title{
        font-size: 2.1rem;
        color: #313131;
        .bold();
        opacity: 0.5;
    }

    &_item{
        font-size: 1.6rem;
        padding-top: 2rem;
        
        img{
            padding-right: 1rem;
        }
    }
}

/*Блок с решениями*/

.dangerous-solutions{
    .size(4.5);
    .size-md(10);
    border: 4px solid #358015;
    padding: 5rem 0;
    padding-left: 5rem;

    &_title{
        font-size: 2.1rem;
        .bold();
    }

    &_item{
        font-size: 1.6rem;
        padding-top: 2rem;

        img{
            padding-right: 1rem;
        }
    }
}

.dangerous-position{
    position: relative;
}

.dangerous-women-img{
    position: absolute;
    width: 30rem;
    bottom: 0;
    left: 50%;
    margin-left: -18rem;
}

/*Материалы*/
.materials{
    background: url('../img/materials/bg-materials.jpg') no-repeat;
    background-size: cover;
}

.materials-titles{
    padding-top: 12rem;
    padding-bottom: 8rem;
    .material-title{
        text-align: center;
        font-size: 3.1rem;
        .bold();
        padding-bottom: 5rem;
    }

    .material-subtitle{
        font-size: 1.9rem;
        text-align: center;
    }
}

.materials-slider{
    padding-bottom: 8rem;
}

.material-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
}

.material-item{
    
    text-align: center;

    &_invis{
        color: transparent;
    }

    img{
        margin: 0 auto;
    }

    &_name{
        padding-top: 1.4rem;
        padding-bottom: 3rem;
        font-size: 1.5rem;
        .bold();
    }

    &_descr{
        font-size: 1.9rem;

        strong{
            .bold();
        }
    }

    &_certificat{
        font-size: 1.6rem;
        padding-top: 3rem;

        a{
            color: @green-light;
            text-decoration: none;
        }

        a:hover{
            text-decoration: underline;
        }
    }
}

.materials-buy-descr{
    text-align: center;
    font-size: 1.9rem;
    padding-bottom: 9rem;

    a{
        text-decoration: none;
        color: @green-light;
    }

    strong{
        .bold();
    }
}

/*Как мы работаем*/
.how-we-work{
    background: url('../img/how-we-work/bg-work.jpg') no-repeat center;
    background-size: cover;
}

.how-we-work-container{
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    padding-top: 12rem;
    padding-bottom: 13rem;
    flex-wrap: wrap;

}

.how-we-work-titles{
    .size(5);
    .size-md(10);
    padding-top: 4rem;
}

.how-we-work-title{
    font-size: 4rem;
    .bold();
    text-transform: uppercase;
}

.how-we-work-question, .how-we-work-descr{
    font-size: 2rem;
}

.how-we-work-question{
    padding-bottom: 1.5rem;
}

.how-we-work-form{
    .size(3.5);
    .size-md(6);
    .size-sm(8);
    text-align: center;
    padding: 4rem 5rem;
    background: #fff;

    form {
        margin-top: 4rem;
    }

    &-header {
        font-size: 1.8rem;
    }

    &-btn {
        margin-bottom: 1.5rem;
        display: block;
        width: 100%;
        height: 6.5rem;
        background-color: @btn-orange;
        border: 1px solid #ffba15;
        box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
        text-transform: uppercase;
        font-size: 1.6rem;
        color: #fff;
        .bold();
        cursor: pointer;
    }

    small{
        font-size: 1.3rem;
        color: #565656;
    }
}

/*Дополниельные услуги*/
.extra-services{
    background: @green-dark;
}

.extra-services-titles{
    text-align: center;
    color: #fff;
    padding-top: 12rem;
    padding-bottom: 7rem;
}

.extra-services-title{
    font-size: 3.6rem;
    .bold();
    padding-bottom: 5rem;
}

.extra-services-subtitle{
    font-size: 1.9rem;
}

.extra-services-items{
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    flex-wrap: wrap;
    padding-bottom: 11rem;
}

.extra-services-item{
    text-align: center;
    .size(4);
    .size-md(6);
    .size-sm(10);

    &-name{
        padding: 3rem 0;
        font-size: 2rem;
        color: #fff;
    }

    &-btn{
        padding: 1.5rem 4rem;
        font-size: 1.6rem;
        .bold();
        text-transform: uppercase;
        background: @btn-orange;
        color: #fff;
        cursor: pointer;
    }

}

/*Контакты*/
.contacts{
    background: url('../img/contacts/contacts-bg.jpg') no-repeat center;
    background-size: cover;
}

.contacts-container{
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    flex-wrap: wrap;
}

.contacts-info{
    padding-top: 8rem;
    padding-bottom: 8rem;
    .size(6);
    .size-md(12);


    &-title{
        font-size: 3.6rem;
        .bold();
        padding-bottom: 7rem;
    }

    &-map{
        width: 100%;
        padding-bottom: 2rem;
    }

    &-adress, &-phone, &-email, &-requisites{
        font-size: 1.8rem;
        padding-bottom: 4rem;
        line-height: 3.5rem;

        strong{
            .bold();
        }
    }

    &-email span{
        text-decoration: underline;
    }
    
}

.contacts-form{
    text-align: center;
    padding: 4rem 5rem;
    background: #fff;
    .size(3.5);
    .size-md(6);
    .size-sm(8);
    margin-top: 8rem;
    margin-bottom: 28rem;

    &-header{
        font-size: 1.8rem;
    }

    form{
        margin-top: 4rem;
    }

    &-btn {
        margin-bottom: 1.5rem;
        display: block;
        width: 100%;
        height: 6.5rem;
        background-color: @btn-orange;
        border: 1px solid #ffba15;
        box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
        text-transform: uppercase;
        font-size: 1.6rem;
        color: #fff;
        .bold();
        cursor: pointer;
    }
}

.contacts-message{
    height: 9rem;
}

/*Футер*/
.footer-container{
    display: flex;
    .justify-content(space-between);
    .justify-content-xs(center);
    align-items: center;
    flex-wrap: wrap;
}

.footer-logo{
    padding: 4rem 0;
}

.footer-contacts{
    .size(3);
    .size-md(4);
    .size-sm(5);
    .size-xs(12);
    text-align: right;

    &-phone{
        font-size: 2.4rem;
        .bold();
    }

    &-email, &-schedule{
        font-size: 1.7rem;
        text-decoration: underline;
    }
}

/*Адаптив*/
@media screen and (max-width:992px) {

    .main-content {
        padding-top: 2rem;
    }

    .main-information {
        text-align: center;

        .main-title {
            font-size: 3.5rem;
            padding-bottom: 2rem;
        }

        .main-subtitle {
            font-size: 2.4rem;
            padding-bottom: 3rem;
        }

        .main-price {
            margin-bottom: 3rem;
        }

        .main-trigger {
            font-size: 2rem;
        }
    }

    .main-form {
        margin: 2rem auto;
    }

    .main-form-footnote {
        text-align: center;
        font-size: 1.2rem;
        margin: 0;
        padding-bottom: 1rem;
        .bold();
    }

    .price-item {
        margin-bottom: 2rem;
        padding: 3rem 2rem;
    }

    .price-btn-wrap {
        margin-top: 3rem;
        text-align: center;
    }


    .hand{
        display: none;
    }

    .bg-work-scheme{
        background: #fff;
    }

    .dangerous-women-img{
        display: none;
    }

    .how-we-work-titles{
        text-align: center;
        margin-bottom: 5rem;
    }

    .extra-services-item{
        padding: 0 0.5rem;
        padding-bottom: 6rem;
    }

    .extra-services-items{
        padding-bottom: 7rem;
    }

    .contacts-info{
        text-align: center;
        padding-bottom: 0;

        &-map{
            width: 50%;
        }
    }

    .contacts-form{
        margin-top: 3rem;
        margin-bottom: 4rem;
    }
}

@media screen and (max-width:720px) {
    .header {
        padding-bottom: 0.5rem;
        flex-wrap: wrap-reverse;


    }

    .phone-section {
        padding-bottom: 3rem;

        span {
            margin-right: 0;
        }
    }

    .wrap-input{
        flex-wrap: wrap;
    }

    .work-scheme-item{
        text-align: center;
    }
    
    .all-in-100-item-descr{
        font-size: 1.6rem;
    }
    
    .dangerous-problems,.dangerous-solutions{
        padding: 3rem;
    }

    .contacts-info-map{
        width: 80%;
    }    
}

@media screen and (max-width: 576px) {
    .main-information {
        .main-title {
            font-size: 2.7rem;
            text-align: center;
        }
    }

